.layout {
  padding: var(--border) var(--border) 0;
  min-height: 100vh;
}

.content {
  margin: 5px auto 0;
  border-radius: 10px;
  padding: 24px;
  max-width: 1440px;
  width: 100%;
}

.wrapper {
  justify-content: space-between;
  gap: 20px;
}

.footer {
  max-width: 1440px;
  margin: 0 auto;
}